import Vue from "vue";

class DataTableSpace {
  constructor({
    pageSize = 80,
    isDeleted = false,

    propsTable,
  }) {
    this.filters['page_size'] = pageSize;
    this.filters['is_deleted'] = isDeleted;

    this.propsTable = Object.assign(this.propsTable, propsTable) ;
  }

  apiLoading = false;
  propsTable = {
    isMarkDeleted: null,
  }; // props table

  countDataTotal = -1;
  listData = [];
  listDataGroup = [];
  activeElement = null;

  linkPageNext = null;
  linkPagePrevious = null;
  
  filters = { //  
    'page_size': '',
    'is_deleted': false,
    'parent__isnull': null,
    'ordering': null,
    'search': null,
    'parent': null,
    'is_group': null,
  };
  filtersExtended = null;
  addingMode = {
    index: null,
    id: null,
  };

  markersEvents = {
    filterExtendedReset: null,
    filterExtendedOff: null,
    eventAdding: null,
    modeAdding: null,
    modeEditing: null,
  };
}

export default {
  // ----/  ------------------------------
  CREATE_TABLE_DATA_SPACE(state, option) {
    Vue.set(state[option.tableName], option.guid, new DataTableSpace(option.tableOption));
    console.log(state[option.tableName]);
  },
  DELETE_TABLE_DATA_SPACE(state, option) {
    delete state[option.tableName][option.guid];
  },
  // ---------------------------------------------------------------------------

  SET_PROPS_TABLE(state, option) {
    for (let key of Object.keys(option.propsTable)) {
      state[option.tableName][option.guid].propsTable[key] = option.propsTable[key];
    }
    console.log(state[option.tableName][option.guid].propsTable);
  },

  SET_ACTIVE_ELEMENT(state, option) {
    state[option.tableName][option.guid].activeElement = (option.value) ? option.value : null;
    // console.log(state[option.tableName][option.guid]);
  },

  SET_LOADING_API(state, option) {
    state[option.tableName][option.guid].apiLoading = option.status;
  },

  SET_OPTIONS(state, option) {
    // console.log(option.data);
    state[option.tableName].listOptions = option.data;
    state[option.tableName].description = option.description;
    if ('parent' in option.data) {
      state[option.tableName][option.guid].propsTable.isHierarchy = (state[option.tableName][option.guid].propsTable.isHierarchy == null) ? 
        null : 
        (state[option.tableName][option.guid].propsTable.isHierarchy && true);
      state[option.tableName][option.guid].filters['parent__isnull'] = true;
      state[option.tableName][option.guid].filters['ordering'] = `-is_group`;
    } else {
      state[option.tableName][option.guid].propsTable.isHierarchy = (state[option.tableName][option.guid].propsTable.isHierarchy == null) ? 
        null :
        (state[option.tableName][option.guid].propsTable.isHierarchy && false);
    }
    state[option.tableName][option.guid].propsTable.isMarkDeleted = ('is_deleted' in option.data) ? true : false;
    // if ('is_deleted' in option.data) {
    //   // Vue.set(state[option.tableName][option.guid].propsTable, isMarkDeleted, true);
    //   state[option.tableName][option.guid].propsTable.isMarkDeleted = true;
    // } else {
    //   // Vue.set(state[option.tableName][option.guid].propsTable, isMarkDeleted, false);
    //   state[option.tableName][option.guid].propsTable.isMarkDeleted = false;
    // }
  },
  SET_EXTRA_ACTIONS(state, option) {
    state[option.tableName].extra_actions = option.data;
  },
  SET_DATA(state, option) {
    if ('guid' in option) state[option.tableName][option.guid].listData.push(option.value);
    else state[option.tableName].listData.push(option.value);
  },
  UPDATE_DATA(state, option) {
    let index = state[option.tableName].listData.findIndex(item => item.id == option.value.id);
    state[option.tableName].listData[index] = option.value;
  },
  SET_DATA_PREVIOUS(state, option) {
    // console.time('For join');
    state[option.tableName][option.guid].listData = option.buferData.concat(state[option.tableName][option.guid].listData);
    // state[option.tableName][option.guid].listData = [...option.buferData, ...state[option.tableName][option.guid].listData];
    // for (let i = 0; i < state[option.tableName][option.guid].listData.length - 1; i++) {
    //   option.buferData.push(state[option.tableName][option.guid].listData[i])
    // }
    // state[option.tableName][option.guid].listData = option.buferData;
    // console.timeEnd('For join');
  },
  SET_DATA_OPTIONS(state, option) {
    state[option.tableName][option.guid].countDataTotal = option.data.count;
    state[option.tableName][option.guid].linkPageNext = option.data.next
  },
  SET_DATA_OPTIONS_PRELOAD(state, option) {
    state[option.tableName][option.guid].countDataTotal = option.data.count;
    state[option.tableName][option.guid].linkPagePrevious = option.data.previous;
  },
  CLEAR_DATA(state, option) {
    state[option.tableName][option.guid].countDataTotal = -1;
    state[option.tableName][option.guid].listData = [];
    state[option.tableName][option.guid].linkPageNext = null;
    state[option.tableName][option.guid].linkPagePrevious = null;
  },
  CLEAR_DATA_GROUP_LEGEND(state, option) {
    state[option.tableName][option.guid].listDataGroup = [];
  },
  CHANGE_DATA_GROUP_LEGEND(state, option) {
    let index = state[option.tableName][option.guid].listDataGroup.findIndex(item => item.id == option.value.id);
    if (index == -1)
      state[option.tableName][option.guid].listDataGroup.push(option.value);
    else
      state[option.tableName][option.guid].listDataGroup = state[option.tableName][option.guid].listDataGroup.slice(0, index);
  },


  // ----    --------------------------------------
  ADDING_NEW_ELEMEN_INLINE(state, option) {
    let newElement = {};
    Object.keys(state[option.tableName].listOptions).forEach(item => {
      newElement[item] = null;
    });
    //   ,   
    let indexLastGroup = state[option.tableName][option.guid].listDataGroup.length - 1;
    if (indexLastGroup > -1) {
      newElement.parent = state[option.tableName][option.guid].listDataGroup[indexLastGroup];
    }
    
    let indexCurrentElement = state[option.tableName][option.guid].listData.findIndex(item => item.id == option.id);

    if (indexCurrentElement == -1) {
      state[option.tableName][option.guid].listData.splice(0, 0, newElement);
      state[option.tableName][option.guid].addingMode.index = 0;
    } else {
      state[option.tableName][option.guid].listData.splice(indexCurrentElement + 1, 0, newElement);
      state[option.tableName][option.guid].addingMode.index = indexCurrentElement + 1;
    }
  },
  // ----    ----------------------------------------
  ADDING_NEW_ELEMEN_INLINE_FIELD(state, option) {
    let index = state[option.tableName][option.guid].listData.findIndex(item => item.id == option.id);
    state[option.tableName][option.guid].listData[index][option.fieldName] = option.value;
  },
  // ----      -----------------------
  DELETING_NEW_ELEMENT_INLINE(state, option) {
    state[option.tableName][option.guid].listData.splice(state[option.tableName][option.guid].addingMode.index, 1);
    state[option.tableName][option.guid].addingMode.id = null;
    state[option.tableName][option.guid].addingMode.index = null;
  },
  // ---------------------------------------------------------------------------
  RESET_ADDING_MODE(state, option) {
    state[option.tableName][option.guid].addingMode.id = null;
    state[option.tableName][option.guid].addingMode.index = null;
  },


  // ----    ----------------------------------
  ADDING_DATA_LINK(state, option) {
    if (state[option.tableName].listData.findIndex(item => item.id == option.value.id) == -1) {
      state[option.tableName].listData.push(option.value);
    }
  },
  // ---------------------------------------------------------------------------

  // ----   -------------------------------------------
  MARK_DELETED_ELEMENT(state, option) {
    let index = state[option.tableName][option.guid].listData.findIndex(item => item.id == option.id);
    state[option.tableName][option.guid].listData.splice(index, 1);
  },
  // ---------------------------------------------------------------------------

  // ----FILTERS----------------------------------------------------------------
  // ----   ----------------------------------------
  SET_FILTER_DEFAULT(state, option) {
    for (let item of Object.entries(option.defaultFilters)) {
      state[option.tableName][option.guid].filters[item[0]] = item[1];
    }
  },
  SET_FILTER_PARENT(state, option) {
    if (state[option.tableName][option.guid].listDataGroup.length) {
      state[option.tableName][option.guid].filters['parent__isnull'] = null;
      state[option.tableName][option.guid].filters['parent'] = state[option.tableName][option.guid].listDataGroup[state[option.tableName][option.guid].listDataGroup.length - 1].id;
    } else {
      state[option.tableName][option.guid].filters['parent__isnull'] = true;
      state[option.tableName][option.guid].filters['parent'] = null;
    }
  },
  // ----  ------------------------------------------
  SET_FILTER_EXTENDED(state, option) {
    state[option.tableName][option.guid].filtersExtended = (option.value != null && option.value != '') ? option.value : null;
  },

  // ---- --------------------------------------------------------
  MARK_EVENT_FILTER_EXTENDED_RESET(state, option) {
    state[option.tableName][option.guid].markersEvents.filterExtendedReset = true;
    setTimeout(() => state[option.tableName][option.guid].markersEvents.filterExtendedReset = null,1000);
  },
  MARK_EVENT_FILTER_EXTENDED_OFF(state, option) {
    state[option.tableName][option.guid].markersEvents.filterExtendedOff = true;
    setTimeout(() => state[option.tableName][option.guid].markersEvents.filterExtendedOff = null,1000);
  },
  MARK_EVENT_ADDING(state, option) {
    state[option.tableName][option.guid].markersEvents.eventAdding = option.status;
    setTimeout(() => state[option.tableName][option.guid].markersEvents.eventAdding = null,1000);
  },
  MARK_MODE_ADDING(state, option) {
    state[option.tableName][option.guid].markersEvents.modeAdding = option.status;
    // setTimeout(() => state[option.tableName][option.guid].markersEvents.eventAdding = null,1000);
  },
}